.button {
	display: inline-grid;
	place-content: center;

	width: max-content;
	height: max-content;
	padding-block: 0.4em;
	padding-inline: 2em;
	border: 2px solid rgb(132, 224, 255);

	background-color: transparent;

	cursor: pointer;
}

.button.small {
	font-size: 0.833rem;
}

.button.big {
	font-size: 0.938rem;
}

.button.squared {
	padding: 0.4em;
}